import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {DebuggerProvider} from "../../providers/debugger/debugger";

/**
 * Generated class for the DebuggerPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-debugger',
  templateUrl: 'debugger.html',
})
export class DebuggerPage {

  area = '';
  constructor(public navCtrl: NavController, public navParams: NavParams, debuggerP:DebuggerProvider) {
    for(let i = 0; i< debuggerP.stack.length; i++) {
      this.area += debuggerP.stack[i] + "\n";
    }
    debuggerP.callback = (content) => {
      this.area += content + "\n";
    }
  }

  ionViewDidLoad() {
  }

}



